import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			headerPosition: "fixed",
			headerTop: "0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0,
			goodTypeId: '', //已选子分类id
			parentTypeId:'', //已选父分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			goodsLists:[],
			goodTypeList:[]
		};
	},

	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		if (e.scrollTop >= 0) {
			this.headerPosition = "fixed";
		} else {
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom() {
		this.loadData();
	},
	methods: {

		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type = 'add', loading) {
			//没有更多直接返回
			if (type === 'add') {
				if (this.loadingType === 'nomore') {
					return;
				}
				this.loadingType = 'loading';
			} else {
				this.loadingType = 'more'
			}

			if (type === 'refresh') {
				this.goodsLists = [];
			}
			//筛选，测试数据直接前端筛选了
			if (this.filterIndex === 1) {
				goodsLists.sort((a, b) => b.goodsaled - a.goodsaled)
			}
			if (this.filterIndex === 2) {
				goodsLists.sort((a, b) => {
					if (this.priceOrder == 1) {
						return a.goodprice - b.goodprice;
					}
					return b.goodprice - a.goodprice;
				})
			}

			this.goodsLists = this.goodsLists.concat(this.goodsLists);//连接两个或多个数组

			//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			this.loadingType = this.goodsLists.length > 0 ? 'nomore' : 'more';
			if (type === 'refresh') {
				if (loading == 1) {
					uni.hideLoading()
				} else {
					uni.stopPullDownRefresh();
				}
			}
		},
		//筛选点击
		tabClick(index) {
			if (this.filterIndex === index && index !== 2) {
				return;
			}
			this.filterIndex = index;
			if (index === 2) {
				this.priceOrder = this.priceOrder === 1 ? 2 : 1;
			} else {
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(() => {
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item) {
			this.goodTypeId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(id) {
			//测试数据没有写id，用title代替
			
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		stopPrevent() {},
		//从后台获取数据
		showGoods() {
			uni.request({
				data: {
					goodTypeId: this.goodTypeId,
					parentTypeId: this.parentTypeId
				},
				method: "GET",
				header: {
					'content-type': 'application/x-www-form-urlencoded;charset=utf-8'
				},
				url: 'http://127.0.0.1:8001/mallgoods/show',
				success: (res) => {
					this.goodsLists = res.data.data;
					this.goodTypeList = res.data.data2;
				}
			})
		},
	},

	onLoad(options) {



		this.goodTypeId = options.tid;
		this.parentTypeId = options.sid;
		this.loadData();
		this.showGoods(this.goodTypeId);
	},
}
